.card {
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(26, 188, 156, 0.5); 
  border-radius: 8px; 
  max-width: 400px; 
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 100vw; 
}

.tag {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-radius: 8px;
  width: 100%; 
}

.form {
  margin-top: 8px; 
  padding: 20px 24px;
}

.submit-btn {
  display: flex;
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 20px;
}

.submit {
  font-size: 18px;
  width: 100%;
  max-width: 300px;
  background-color: rgba(26, 188, 156, 0.7);
  color: #fff; 
  border: none; 
  border-radius: 4px; 
}

.submit:hover {
  background-color: rgba(26, 188, 156, 0.9); 
}

.avatar {
  margin: 8px 0;
  background-color: rgba(26, 188, 156, 0.7);
  color: #fff; 
}
